@charset "UTF-8";
.matrix {
  display: grid;
  gap: 5px;
}

/* Row styling */
.row {
  display: flex;
  gap: 5px;
  flex-direction: row;
}

/* Cell styling */
.cell {
  width: 80px;
  height: 80px;
  border: 1px solid #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  cursor: pointer;
  position: relative;
  text-align: center;
}

.selected {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  text-align: center;
}

.cell-name {
  position: absolute;
}

.cell-input {
  width: 40px;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 0 2px;
}

.cell.editing {
  background-color: #fff3cd;
  border-color: #ffc107;
}

/* Input field inside editing cells */
.cell input {
  width: 100%;
  height: 100%;
  border: none;
  text-align: center;
  font-size: 14px;
  background-color: transparent;
}

.cell input:focus {
  outline: none;
}

/* Màu sắc cho các tùy chọn mới */
.has-error {
  border: 2px solid red;
}

/* Icon styling */
.icon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

/* Animation cho xuất hiện icon */
@keyframes showIcon {
  from {
    filter: blur(10px);
    opacity: 0;
  }
  to {
    filter: blur(0);
    opacity: 1;
  }
}
.show-icon {
  animation: showIcon 0.3s forwards;
}

/* Animation for changing status */
@keyframes statusChange {
  from {
    filter: blur(10px);
    opacity: 0;
  }
  to {
    filter: blur(0);
    opacity: 1;
  }
}
@keyframes statusRevert {
  from {
    filter: blur(10px);
    opacity: 0;
  }
  to {
    filter: blur(0);
    opacity: 1;
  }
}
.status-available {
  animation: statusChange 0.3s forwards;
}

.status-block {
  animation: statusChange 0.3s forwards;
}

.status-reverting {
  animation: statusRevert 0.3s forwards;
}
